<template>
<div>
  <strong>Question {{ questionNumber }}:</strong><br/><br/>
  <strong> {{question.question}} </strong><br/><br/>
  <div v-for="(mcanswer,index) in question.incorrect_answers" :key="index">
    <input type="radio" :id="'answer'+index" name="currentQuestion" v-model="answer" :value="mcanswer"><label :for="'answer'+index">{{mcanswer}}</label><br/><br/>
  </div>

  <button @click="submitAnswer">Answer</button>
</div>
</template>

<script>
export default {
  name: 'question',
  data() {
     console.log(this.question.incorrect_answers);
     return {
       answer:''
     }
  },
	props:['question','question-number'],
	methods:{
		submitAnswer:function() {
			this.$emit('answer', {answer:this.answer});
      this.answer = null;
		}
	}
}
</script>

